import { Box, Button, TextField, Typography } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { SET_COMMENT } from '../../modules/reducer/personalityTestReducer';

const PersonalityTestComment = function () {
  const test = useSelector((s) => s.personalityTest);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const handleOnchangeInput = (e) => {
    dispatch({ type: SET_COMMENT, payload: e.target.value });
  };

  return (
    <Box>
      <Button
        size='medium'
        onClick={() => {
          window.confirm(' ?') && navigate(-1);
        }}
        variant='contained'
        sx={{ marginTop: 3 }}
      >
        
      </Button>
      <Typography
        variant='h6'
        mt={5}
      >
          .[] (<span>{test.comment.length}</span>
        /200)
      </Typography>
      <TextField
        multiline
        fullWidth
        maxRows={5}
        minRows={5}
        name='test-comment'
        value={test.comment}
        onChange={handleOnchangeInput}
        error={test.comment.length > 200}
        placeholder='       ! &#13;&#10;.'
        helperText={
          test.comment.length > 200 ? '200  .' : null
        }
        sx={{ whiteSpace: 'pre-line' }}
      />
      <Button
        fullWidth
        variant='contained'
        sx={{ mt: 10 }}
        disabled={test.comment.length > 200}
        onClick={() => {
          if (test.comment.length > 200) {
            alert('  200     .');
          }
          window.confirm(' ?') &&
            navigate('/personality-test/result');
        }}
      >
        
      </Button>
    </Box>
  );
};
export default PersonalityTestComment;
